import React, { useContext, useEffect, useMemo, useState } from 'react';
import { Affix, Button, InputNumber, List } from 'antd';
import answerList from '../components/AnswerList';
import CustomHeader from '../components/CustomHeader';
import styled from 'styled-components';
import { SearchContext } from '../search.context';
import { ThemeContext } from '../theme.context';
const CustomerContent = styled.div`
  @media (min-width: 961px) {
    max-width: 70%;
  }
  word-break: break-word;
  font-weight: 500;
`;
function Item({ item, index }) {
  return (
    <List.Item actions={answerList(item)}>
      <List.Item.Meta title={`Câu thứ ${index + 1}`} />
      <CustomerContent>{item.noidung}</CustomerContent>
    </List.Item>
  );
}
const ContainerSub = styled.div`
  margin-bottom: 20px;
  & .ant-list-item-action {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }
  & .ant-list-item-action > li {
    display: block;
    padding: 0;
    text-align: start;
  }
  & .ant-list-item-action-split {
    display: none;
  }
`;
function filter(text) {
  const reg = new RegExp(text);
  return ({ noidung }) => noidung.match(reg);
}
const prevPage = Number.parseInt(localStorage.getItem('pageSize') || '10', 10);
function QuestionPage({ data }) {
  const [text, setText] = useState('');
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(prevPage);
  const [dark] = useContext(ThemeContext);
  const renderData = useMemo(() => data.filter(filter(text)), [text]);
  const goToPrev = () => page > 1 && setPage(page - 1);
  const goToNext = () => setPage(page + 1);
  useEffect(() => {
    localStorage.setItem('pageSize', pageSize);
  }, [pageSize]);
  const Container = useMemo(
    () => styled(ContainerSub)`
      li {
        ${dark ? { color: '#dbd8e3' } : {}}
        .ant-list-item-meta-title {
          ${dark ? { color: '#dbd8e3' } : {}}
        }
      }
    `,
    [dark]
  );
  useEffect(() => {
    const listener = e => {
      if (e.keyCode === 38) {
        // up arrow
      } else if (e.keyCode === 40) {
        // down arrow
      } else if (e.keyCode === 37) {
        goToPrev();
        // left arrow
      } else if (e.keyCode === 39) {
        goToNext();
        // right arrow
      }
    };
    document.addEventListener('keydown', listener);
    return () => document.removeEventListener('keydown', listener);
  }, [page]);
  const buttonStyle = useMemo(() => {
    return {
      backgroundColor: '#fff0',
      ...(dark ? { color: 'white' } : { color: 'black' }),
    };
  }, [dark]);
  return (
    <SearchContext.Provider value={[text, setText]}>
      <Affix offsetTop={500} style={{ position: 'absolute', right: 40 }}>
        <Button
          type="dashed"
          style={buttonStyle}
          onClick={goToNext}
          icon="right"
        />
      </Affix>
      <Affix offsetTop={500} style={{ position: 'absolute', left: 0 }}>
        <Button
          type="dashed"
          style={buttonStyle}
          onClick={goToPrev}
          icon="left"
        />
      </Affix>
      <Container id="start">
        <List
          header={<CustomHeader />}
          itemLayout="vertical"
          size="large"
          pagination={{
            onChange(page) {
              setPage(page);
              window.location.hash = 'start';
              setTimeout(() => {
                window.location.hash = '';
              }, 200);
            },
            current: page,
            pageSize ,
          }}
          dataSource={renderData}
          renderItem={(item, i) => (
            <Item item={item} index={(page - 1) * 5 + i} />
          )}
        />
        <InputNumber
          min={1}
          max={400}
          value={pageSize}
          onChange={value => {
            if (!value) {
              setPageSize(1);
            } else {
              setPageSize(value);
            }
          }}
        />
      </Container>
    </SearchContext.Provider>
  );
}

export default QuestionPage;
