import React, { useCallback, useEffect, useMemo, useState } from 'react';
import './index.css';
import './App.css';
import QuestionPage from './pages/QuestionPage';
import Intro from './pages/Intro';
import { Affix, Button, Col, Row } from 'antd';
import { ThemeContext } from './theme.context';
import classNames from 'classnames';

function App() {
  const [isLoading, setLoading] = useState(true);
  const [data, setData] = useState([]);
  const [theme, setTheme] = useState(!!localStorage.getItem('theme'));
  const buttonStyle = useMemo(() => {
    return {
      backgroundColor: '#fff0',
      ...(theme ? { color: 'white' } : { color: 'black' }),
    };
  }, [theme]);
  useEffect(() => {
    import('./data.json')
      // .then(
      //   data =>
      //     new Promise(resolve => {
      //       setTimeout(() => resolve(data), 200);
      //     })
      // )
      .then(data => setData(data.entries.entry))
      .finally(() => setLoading(false));
  }, []);
  if (isLoading) return <Intro />;
  return (
    <ThemeContext.Provider value={[theme, setTheme]}>
      <div className={classNames({ dark: theme })}>
        <Row>
          <Affix offsetTop={20} style={{ position: 'absolute', right: 10 }}>
            <Button type="dashed" href="#start" style={buttonStyle} icon="up" />
          </Affix>
          <Col span={22} offset={1}>
            <QuestionPage data={data} />
          </Col>
        </Row>
      </div>
    </ThemeContext.Provider>
  );
}

export default App;
